import { MenuIcon, X } from "lucide-react";
import React, { useState, useRef, useEffect } from "react";
import NavLink from "./NavLink";
import Link from "next/link";

export default function MenuButton() {
  const [menuOpen, setMenuOpen] = useState<boolean>(false);
  const menuRef = useRef<HTMLDivElement | null>(null);

  useEffect(() => {
    function handleClickOutside(event: MouseEvent) {
      if (menuRef.current && !menuRef.current.contains(event.target as Node)) {
        setMenuOpen(false);
      }
    }

    document.addEventListener("mousedown", handleClickOutside);
    return () => document.removeEventListener("mousedown", handleClickOutside);
  }, []);

  return (
    <section>
      <button
        onClick={() => setMenuOpen(true)}
        className="cursor-pointer text-[#D0D7F8] sm:hidden"
      >
        <MenuIcon size={33} strokeWidth={3} />
      </button>

      <div
        ref={menuRef}
        className={`absolute transition-all duration-300 ease-in-out ${
          menuOpen ? "w-[60%]" : "w-0"
        } h-full top-0 right-0 bg-blue-800`}
      >
        <header className="flex p-6 items-center justify-end">
          <button onClick={() => setMenuOpen(false)} className="cursor-pointer">
            <X size={30} />
          </button>
        </header>

        <nav>
          <ul className="flex flex-col barlowCondensed">
            <Link href={"/"} className=" relative px-6 h-18 flex items-center">
              <li className="flex items-center uppercase gap-1 text-2xl">
                <p>00</p>
                <p>Home</p>
              </li>

              <div className="h-full left-0 w-1 absolute bg-white"></div>
            </Link>
          </ul>
        </nav>
      </div>
    </section>
  );
}
